body {
  margin: 0;
  background: #0f0f0f;
  font-family: Arial, sans-serif;
}

section {
  min-height: 100vh;
  padding: 80px 10%;
  color: white;
}

h1, h2 {
  text-align: center;
}

.card {
  background: #151515;
  padding: 25px;
  border-radius: 15px;
  box-shadow: 0 15px 40px rgba(0,0,0,0.4);
}